.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#container-grafite{
  background-color: #2e2e2e;
  width: auto;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;

}

.itens{
  /* background-color: white; */
  height: 100px;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imagens:hover{
  width: 50px;
  height: 50px;
}

.imagens{
  cursor: pointer;
}

